          fileSize: file.file.size,
          fileName: file.file.name,
          channelName: uploadInfo.channelName
        }, {
          withAuthCode: true,
          signal: abortController.signal
        });
        file.onSuccess({
          data: [{
            src: commitRes.data.src
          }]
        }, file.file);
      } catch (err) {
        if (err.name !== 'CanceledError') {
          this.exceptionList.push(file);
          file.onError(err, file.file);
        }
      } finally {
        this.abortControllers.delete(file.file.uid);
        this.onUploadComplete();
      }
    },
    async uploadToHuggingFaceMultipart(file, uploadInfo, abortController) {
      const {
        uploadAction
      } = uploadInfo;
      const chunkSize = parseInt(uploadAction.header.chunk_size);
      const parts = Object.keys(uploadAction.header).filter(k => /^[0-9]+$/.test(k));
      const completeParts = [];
      for (const part of parts) {
        if (abortController.signal.aborted) throw new DOMException('Cancelled', 'AbortError');
        const idx = parseInt(part) - 1;
        const chunk = file.file.slice(idx * chunkSize, Math.min((idx + 1) * chunkSize, file.file.size));
        const res = await fetch(uploadAction.header[part], {
          method: 'PUT',
          body: chunk,
          signal: abortController.signal
        });
        completeParts.push({
          partNumber: parseInt(part),
          etag: res.headers.get('ETag')
        });
      }
      await fetch(uploadAction.href, {
        method: 'POST',
        body: JSON.stringify({
          oid: uploadInfo.oid,
          parts: completeParts
        }),
        headers: {
          'Content-Type': 'application/vnd.git-lfs+json'
        },
        signal: abortController.signal
      });
    },
    async computeSha256(file) {
      const sha256 = this.createSha256();
      const CHUNK_SIZE = 4 * 1024 * 1024;
      let offset = 0;
      while (offset < file.size) {
        sha256.update(new Uint8Array(await file.slice(offset, offset + CHUNK_SIZE).arrayBuffer()));
        offset += CHUNK_SIZE;
      }
      return sha256.digest();
    },
    createSha256() {
      // Minimal SHA256 implementation
      const K = [0x428a2f98, 0x71374491, 0xb5c0fbcf, 0xe9b5dba5, 0x3956c25b, 0x59f111f1, 0x923f82a4, 0xab1c5ed5, 0xd807aa98, 0x12835b01, 0x243185be, 0x550c7dc3, 0x72be5d74, 0x80deb1fe, 0x9bdc06a7, 0xc19bf174, 0xe49b69c1, 0xefbe4786, 0x0fc19dc6, 0x240ca1cc, 0x2de92c6f, 0x4a7484aa, 0x5cb0a9dc, 0x76f988da, 0x983e5152, 0xa831c66d, 0xb00327c8, 0xbf597fc7, 0xc6e00bf3, 0xd5a79147, 0x06ca6351, 0x14292967, 0x27b70a85, 0x2e1b2138, 0x4d2c6dfc, 0x53380d13, 0x650a7354, 0x766a0abb, 0x81c2c92e, 0x92722c85, 0xa2bfe8a1, 0xa81a664b, 0xc24b8b70, 0xc76c51a3, 0xd192e819, 0xd6990624, 0xf40e3585, 0x106aa070, 0x19a4c116, 0x1e376c08, 0x2748774c, 0x34b0bcb5, 0x391c0cb3, 0x4ed8aa4a, 0x5b9cca4f, 0x682e6ff3, 0x748f82ee, 0x78a5636f, 0x84c87814, 0x8cc70208, 0x90befffa, 0xa4506ceb, 0xbef9a3f7, 0xc67178f2];
      let H = [0x6a09e667, 0xbb67ae85, 0x3c6ef372, 0xa54ff53a, 0x510e527f, 0x9b05688c, 0x1f83d9ab, 0x5be0cd19];
      const buffer = new Uint8Array(64);
      let bufferLength = 0;
      let totalLength = 0;
      const processBlock = block => {
        const W = new Uint32Array(64);
        for (let i = 0; i < 16; i++) W[i] = block[i * 4] << 24 | block[i * 4 + 1] << 16 | block[i * 4 + 2] << 8 | block[i * 4 + 3];
        for (let i = 16; i < 64; i++) {
          const s0 = (W[i - 15] >>> 7 | W[i - 15] << 25) ^ (W[i - 15] >>> 18 | W[i - 15] << 14) ^ W[i - 15] >>> 3;
          const s1 = (W[i - 2] >>> 17 | W[i - 2] << 15) ^ (W[i - 2] >>> 19 | W[i - 2] << 13) ^ W[i - 2] >>> 10;
          W[i] = W[i - 16] + s0 + W[i - 7] + s1 | 0;
        }
        let [a, b, c, d, e, f, g, h] = H;
        for (let i = 0; i < 64; i++) {
          const S1 = (e >>> 6 | e << 26) ^ (e >>> 11 | e << 21) ^ (e >>> 25 | e << 7);
          const ch = e & f ^ ~e & g;
          const temp1 = h + S1 + ch + K[i] + W[i] | 0;
          const S0 = (a >>> 2 | a << 30) ^ (a >>> 13 | a << 19) ^ (a >>> 22 | a << 10);
          const maj = a & b ^ a & c ^ b & c;
          const temp2 = S0 + maj | 0;
          h = g;
          g = f;
          f = e;
          e = d + temp1 | 0;
          d = c;
          c = b;
          b = a;
          a = temp1 + temp2 | 0;
        }
        H[0] = H[0] + a | 0;
        H[1] = H[1] + b | 0;
        H[2] = H[2] + c | 0;
        H[3] = H[3] + d | 0;
        H[4] = H[4] + e | 0;
        H[5] = H[5] + f | 0;
        H[6] = H[6] + g | 0;
        H[7] = H[7] + h | 0;
      };
      return {
        update(data) {
          totalLength += data.length;
          let offset = 0;
          if (bufferLength > 0) {
            const needed = 64 - bufferLength;
            const toCopy = Math.min(needed, data.length);
            buffer.set(data.subarray(0, toCopy), bufferLength);
            bufferLength += toCopy;
            offset = toCopy;
            if (bufferLength === 64) {
              processBlock(buffer);
              bufferLength = 0;
            }
          }
          while (offset + 64 <= data.length) {
            processBlock(data.subarray(offset, offset + 64));
            offset += 64;
          }
          if (offset < data.length) {
            buffer.set(data.subarray(offset), 0);
            bufferLength = data.length - offset;
          }
        },
        digest() {
          const bitLength = totalLength * 8;
          buffer[bufferLength++] = 0x80;
          if (bufferLength > 56) {
            buffer.fill(0, bufferLength, 64);
            processBlock(buffer);
            bufferLength = 0;
          }
          buffer.fill(0, bufferLength, 56);
          const view = new DataView(buffer.buffer);
          view.setUint32(56, Math.floor(bitLength / 0x100000000), false);
          view.setUint32(60, bitLength >>> 0, false);
          processBlock(buffer);
          return H.map(h => h.toString(16).padStart(8, '0')).join('');
        }
      };
    },
    convertImageToWebp(file) {
      return new Promise((resolve, reject) => {
        if (file.type.match(/gif|svg|webp/)) return resolve(null);
        const img = new Image();
        const canvas = document.createElement('canvas');
        img.onload = () => {
          canvas.width = img.width;
          canvas.height = img.height;
          canvas.getContext('2d').drawImage(img, 0, 0);
          canvas.toBlob(blob => {
            if (blob) {
              const newName = file.name.substring(0, file.name.lastIndexOf('.')) + '.webp';
              const f = new File([blob], newName, {
                type: 'image/webp'
              });
              f.uid = file.uid;
              resolve(f);
            } else reject(new Error('WebP Fail'));
          }, 'image/webp', 0.92);
        };
        img.onerror = () => reject(new Error('Img Load Fail'));
        img.src = URL.createObjectURL(file);
      });
    },
    handlePaste(e) {
      if (e.target.tagName === 'INPUT' || e.target.tagName === 'TEXTAREA') return;
      if (e.clipboardData.items.length) this.uploadFromUrl(e.clipboardData.items);
    },
    handleUploadPasteUrls() {
      const urls = this.pastedUrls.split('\n').map(u => u.trim()).filter(u => u && /^(https?:\/\/[^\s$.?#].[^\s]*)$/.test(u));
      if (this.pasteUploadMethod === 'save') {
        this.uploadFromUrl(urls.map(u => ({
          kind: 'string',
          getAsString: cb => cb(u)
        })));
      } else {
        urls.forEach((url, i) => {
          const file = new File([], url.split('/').pop(), {
            type: 'text/plain'
          });
          file.uid = Date.now() + i;
          file.url = url;
          file.finalURL = url;
          file.status = 'uploading';
          file.uploadChannel = 'external';
          this.fileList.push(file);
          // Simulate async success since it's just external link
          setTimeout(() => this.handleSuccess({}, file), 500);
        });
      }
    },
    uploadFromUrl(items) {
      for (let i = 0; i < items.length; i++) {
        if (items[i].kind === 'file') {
          const f = items[i].getAsFile();
          f.uid = Date.now() + i;
          this.beforeUpload(f).then(nf => {
            if (nf) this.uploadFile({
              file: nf,
              onProgress: e => this.handleProgress(e),
              onSuccess: (r, f) => this.handleSuccess(r, f),
              onError: (e, f) => this.handleError(e, f)
            });
          });
        } else if (items[i].kind === 'string') {
          items[i].getAsString(text => {
            if (/^(https?:\/\/[^\s$.?#].[^\s]*)$/.test(text)) {
              axios.post('/api/fetchRes', {
                url: text
              }, {
                responseType: 'blob'
              }).then(r => {
                if (r.status === 200) {
                  // ... (Filename extraction simplified for brevity, assume similar logic)
                  let fileName = 'Pasted.' + (r.data.type.split('/')[1] || 'bin');
                  const f = new File([r.data], fileName, {
                    type: r.data.type
                  });
                  f.uid = Date.now() + i;
                  this.beforeUpload(f).then(nf => {
                    if (nf) this.uploadFile({
                      file: nf,
                      onProgress: e => this.handleProgress(e),
                      onSuccess: (r, f) => this.handleSuccess(r, f),
                      onError: (e, f) => this.handleError(e, f)
                    });
                  });
                }
              });
            }
          });
        }
      }
    },
    copyText(text) {
      navigator.clipboard.writeText(text).then(() => this.$message.success(''));
    },
    selectAllText(e) {
      e.target.select();
    },
    isImage(n) {
      return /jpg|jpeg|png|gif|bmp|webp|svg|avif|heic/i.test(n.split('.').pop());
    },
    isVideo(n) {
      return /mp4|webm|ogg|mkv/i.test(n.split('.').pop());
    }
  }
};